@import '../../App';

.ourservices {
    text-align: center;
    margin: 0 40px;

    h1 {
        color: #242424;
        font-family: Muli;
        font-weight: bold;
        font-size: 30px;
        margin-top: 70px; }

    img {
        margin-top: 50px; }

    h2 {
        font-family: Roboto;
        font-weight: bold;
        font-size: 20px;
        margin-top: 30px;
        text-align: left; }

    p {
        font-family: Roboto;
        font-size: 16px;
        line-height: 24px;
        text-align: left;
        margin-top: 20px; }

    .information-button-container {
        text-align: left;
        margin-top: 20px;
        margin-bottom: 30px; }

    button {
        border: 1px solid $primary-color;
        color: $primary-color;
        padding: 13px 32px;
        outline: none;
        border-radius: 4px;
        font-family: Roboto;
        cursor: pointer;
        text-transform: unset;
        font-weight: normal;
        font-size: 15px;

        &:hover {
            background-color: $primary-color;
            color: white; } }

    .book-now {
        float: initial;
        background-color: $primary-color;
        padding: 13px 82px;
        outline: none;
        border: none;
        border-radius: 4px;
        color: white;
        font-family: Roboto;
        cursor: pointer;
        text-transform: unset;
        font-weight: 300;
        font-size: 15px;
        margin-bottom: 100px;
        margin-top: 20px;

        &:hover {
            background-color: #008888; } } }

@media only screen and (max-width: 350px) {
    .ourservices {
        img {
            width: 230px; } } }

@media only screen and (min-width: 480px) {
    .ourservices {
        margin: 0 80px; } }

@media only screen and (min-width: 600px) {
    .ourservices {
        margin: 0 auto;
        max-width: 443px; } }

@media only screen and (min-width: 768px) {
    .ourservices {
        margin: 0 auto;
        max-width: 850px;

        .book-now {
            margin-top: 100px; }

        h1 {
            margin-top: 100px;
            font-size: 40px; }

        .img-services1, .img-services3, .img-services5, .img-services7 {
            margin-right: 30px; }

        .img-services2, .img-services4, .img-services6 {
            margin-left: 30px; }

        .our-services-flex {
            display: flex;
            align-items: flex-start;
            justify-content: space-around;
            margin-top: 90px;
            max-width: 850px;
            margin-right: 20px;
            margin-left: 20px;

            p {
                font-size: 14px;
                line-height: 21px; }

            img {
                margin-top: 0px; }

            .our-services-flex-text {
                h2 {
                    margin-top: 0px; } } }

        .our-services-flex:nth-child(3), .our-services-flex:nth-child(5), .our-services-flex:nth-child(7) {
            flex-direction: row-reverse; } } }

@media only screen and (min-width: 1200px) {
    .ourservices {
        .our-services-flex {
            margin-right: auto;
            margin-left: auto; } } }








