@import '../../App';

.header {
    background: url('../../assets/header.png') no-repeat;
    background-size: 100% 100%;
    text-align: center;
    margin-top: 70px;

    .header-wide {
        max-width: 1300px;
        margin: 0 auto; }

    h1 {
        padding-top: 130px;
        font-family: Muli;
        font-weight: 800;
        font-size: 40px;
        line-height: 45px;
        color: white;
        width: 335px;
        margin: auto; }

    p {
        font-family: Roboto;
        font-size: 16px;
        line-height: 23px;
        color: white;
        font-weight: 300;
        margin-top: 20px;
        margin-right: auto;
        margin-left: auto;
        width: 330px; }

    button {
        background-color: $primary-color;
        padding: 13px 56px;
        outline: none;
        border: none;
        border-radius: 4px;
        color: white;
        font-family: Roboto;
        cursor: pointer;
        text-transform: unset;
        font-weight: 300;
        margin: 60px 0;
        font-size: 15px;

        &:hover {
            background-color: #008888; } } }

@media only screen and (max-width: 350px) {
    .header {
        margin-top: 60px;
        h1 {
            font-size: 36px;
            width: 300px; }

        p {
            width: 310px; } } }

@media only screen and (min-width: 480px) {
    .header {
        h1 {
            font-size: 46px;
            width: 380px;
            line-height: 52px; }

        p {
            line-height: 26px; } } }

@media only screen and (min-width: 600px) {
    .header {
        background: url('../../assets/headerwide.png') no-repeat;
        background-size: 100% 100%;
        text-align: left;

        h1 {
            margin-left: 85px;
            padding-top: 180px; }

        p {
            margin-left: 85px;
            width: 360px; }

        button {
            margin-left: 85px;
            margin-top: 30px;
            margin-bottom: 180px; } } }

@media only screen and (min-width: 768px) {
    .header {
        h1 {
            font-size: 52px;
            margin-left: 100px;
            width: 420px;
            line-height: 62px; }

        p {
            margin-left: 100px;
            width: 420px;
            line-height: 28px; }

        button {
            margin-left: 100px; } } }

@media only screen and (min-width: 900px) {
    .header {
        h1 {
            font-size: 58px;
            margin-left: 130px;
            width: 480px; }

        p {
            margin-left: 130px; }

        button {
            margin-left: 130px; } } }

@media only screen and (min-width: 1024px) {
    .header {
        h1 {
            font-size: 62px;
            margin-left: 130px;
            line-height: 72px;
            width: 500px; }

        p {
            margin-left: 130px;
            line-height: 30px;
            width: 500px; }

        button {
            margin-left: 130px; } } }

@media only screen and (min-width: 1200px) {
    .header {
        background-attachment: fixed;

        h1 {
            font-size: 65px;
            margin-left: 180px;
            line-height: 72px;
            width: 530px; }

        p {
            margin-left: 180px;
            line-height: 30px;
            width: 500px; }

        button {
            margin-left: 180px;
            margin-bottom: 210px; } } }
